import React, { useState, useEffect, useRef, useLayoutEffect, useCallback } from 'react';
// this is only for class components....   import * as ReactDOM from 'react-dom'; 

// ---------- START snip to copy into react code -----------------------
// for react project using package.json .....    import React from 'react';
// for react project using package.json .....    import * as ReactDOM from 'react-dom'; 
// for codepen.io project .... import * as React from "https://cdn.skypack.dev/react@17.0.1";
// for codepen.io project .... import * as ReactDOM from "https://cdn.skypack.dev/react-dom@17.0.1";
////////////////////////////////////////////////////////////////////
const LABEL = "thur 8"
const MOUSEMOVETHRESHOLD = 10; 
// SEE EXPLANATION AT END HOW  CODEPEN IMPORTS NPM PACKAGES. PACKAGE.JSON IS NOT USED.
function ReactKnob( props ) {
  const UNINIT_A = 12;
  var geomAngle = useRef(-1); var knobAngle = useRef(-1);
  // useState re-renders the control when a var get changed. (queues for rendering, not immediate)
  var [mouseX_st, setmouseX_st] = useState(-1000); 
  var [mouseY_st, setMouseY_st] = useState(-1000);
  var lastX=UNINIT_A; var lastY=0; 
  var isDragging = useRef(false); 
  const thisReactNode = useRef();
  var canvasA = useRef(); // useRef let it keep value between renders. Dont forget to use "current"
  var canvasWidthHeight = useRef(UNINIT_A);
  var displayA = useRef();
  // const [state, dispatch] = useReducer(reducer, initialArg, init);

  /* React function component has advantage over class component because it lets you access dom 
   * components easily [search for "thisReactNode" to see how it works]
   * There are 3 'hook functions' used here for react function components:
   *    useState(), useRef() and useEffect. I'm sure these names mean lots to the designers but they dont say much to me.
   * Below is how they are used in this project, but are not complete descriptions:
   * 1. "useEffect" is the main lifecycle method of function components. 
   *   It invokes custom code when component renders, conditionally according to second array arg:
   *   + missing: run every time. Good for getting dimensions of window etc
   *   + [] empty: run only once when component mounts like componentDidMount
   *   + [aa, bb]: do only when aa or bb changes
   *   + [] empty and returns function: runs when component unmounts
   * 2. "useState" sets up a variable which perists between renders and queues a render when its value is changed.
   * 3. "useRef" lets you name a dom element and reference it in the code.
   */
  // one-time setup of event listeners
  const componentDidMountX = useCallback(() => {  // to avoid error when calling from useEffect()
    // function componentDidMountX() { // from class component componentDidMount()
    window.addEventListener("mousemove", (mevent) =>  { doMoveTouchOrMouse(
      mevent, thisReactNode.current, mevent.clientX, mevent.clientY);});
    thisReactNode.current.addEventListener("mousedown", (mouseEvt) =>  { 
        freezeScroll(true); isDragging.current=true; }); 
    window.addEventListener("mouseup", (mouseEvt) =>  { 
        freezeScroll(false); isDragging.current=false; }); 
        
    thisReactNode.current.addEventListener("touchstart", (mouseEvt) => { 
        freezeScroll(true); isDragging.current=true; }); 
    // touchend or mouseup is for window in case it happens outside of component
    window.addEventListener("touchend", (mouseEvt) =>   { 
        freezeScroll(false); isDragging.current=false; });

    window.addEventListener("touchmove", (mevent) =>  { doMoveTouchOrMouse(
        mevent, thisReactNode.current, mevent.touches[0].clientX, mevent.touches[0].clientY);});
        
    window.addEventListener('resize', () =>  { 
      window.location.reload(); 
    });
  }, [])

  useEffect(() => { // [] empty: run only once when component mounts like componentDidMount
      componentDidMountX(); // setup event listeners
      var borderWidth = parseFloat(thisReactNode.current.parentNode.style.borderWidth); // "4px -> 4"
      canvasWidthHeight.current = thisReactNode.current.parentNode.clientWidth - borderWidth * 2; // offsetWidth  .clientWidth; 
      console.log('canvasWidthHeight.current ' + canvasWidthHeight.current)
    }, [componentDidMountX]);

  useLayoutEffect(() => { // [] empty: run only once when component mounts like componentDidMount
    // console.log('useLayoutEffect')
    }, []);

  useEffect(() => {  // gets called when redrawing if mouseX_st or mouseY_st changes
    // console.log('yyyy ' + mouseX_st)
  }, [mouseX_st, mouseY_st]);

  useEffect(() => {  // runs every time. Draws the stuff in canvas according to x,y mouse pos.
    // the component has been drawn but the canvas has not yet been drawn, so draw it.
    drawCanvas();
    //displayA.current.style.display = 'grid';
    //displayA.current.style.top = (canvasWidthHeight.current * 0.5) + "px";
  });

  useEffect(() => { // [] empty and returns function: component unmounted
    return (() => { console.log('component unmounted') })
  }, []);

  
 
  // TODO dimensions change a bit when scrollbar gets hidden and looks bad
  // This prevents screen from squishing around while knob is turned.
  function freezeScroll(trueOnfalseOff) { if (trueOnfalseOff)  document.body.style.overflow = "hidden"; 
    else document.body.style.overflow = "auto";  }

  function doMoveTouchOrMouse(evt, theNode, x, y) {
    // console.log(isDragging)
    if (!isDragging.current) return;
    // evt.preventDefault(); // prevents screen from scrolling and messing up knob turn
    // shift frame of reference to the active node instead of screen
    var mouseX = x - theNode.offsetLeft;
    // y is lower at top and increases down. If window is scrolled, messes up Y coord, so compensate with scrollY.
    var mouseY = canvasWidthHeight.current - (y - theNode.offsetTop) - window.scrollY; 
    mouseY = mouseY.toFixed(0)
    setmouseX_st(mouseX); setMouseY_st(mouseY); // cause a REDRAW!
    var delta = Math.abs(Math.abs(mouseX) - Math.abs(lastX)) +  Math.abs(Math.abs(mouseY) - Math.abs(lastY))
    // reduce load on client by not responding to every move
    // if (lastX !== UNINIT_A5 && delta < MOUSEMOVETHRESHOLD) { return; }
    lastX = mouseX;  lastY =  mouseY;
    return true;
  }

  function drawCanvas() {
    canvasA.current.width = canvasA.current.height = canvasWidthHeight.current;
    const drawContextA = canvasA.current.getContext('2d');
    drawContextA.clearRect(0, 0, canvasA.current.width, canvasA.current.height);
    const center = canvasWidthHeight.current / 2; 
    const lineWidth = props.lineWidthPct / 100 * canvasWidthHeight.current;
    const radius = center - (lineWidth / 2);
    drawContextA.lineWidth = lineWidth;
    var zzx = (mouseX_st == -1000) ? center : mouseX_st;
    var zzy = (mouseY_st == -1000) ? center + 100 : mouseY_st;
    var dx = zzx - center; 
    var dy = zzy - center;
    geomAngle.current = Math.atan(dy/dx);
    if (dx < 0.0) geomAngle.current -= Math.PI;
    // geometry has angle starting at 3:00 going up toward 12 counterclockwise. Change to where TopDeadCenter is 0 and clockwise increases.
    knobAngle.current =  Math.PI/2 - geomAngle.current; // -this.angle; // degsToRadians(-90) - this.angle + Math.PI/2;
    // knobAngle.current = toDegClock(knobAngle.current);
    if (knobAngle.current == 360) knobAngle.current = 0;
    //if (Math.PI -geomAngle < ) knobAngle.current = 0;
    props.knobCallback(knobAngle.current)
    // this.angle = radiansToDeg(this.angle).toFixed(0)
    //drawContextA.lineWidth = 22;
    drawContextA.strokeStyle =  '#ff0000';
    drawContextA.beginPath();
    drawContextA.moveTo(center, center);
    // normalize line length to radius
    var ratio = radius/Math.pow(Math.pow(dx, 2) + Math.pow(dy, 2),  0.5);
    drawContextA.lineTo(center + dx * ratio, center - dy * ratio); // Y is upside down, so subtract!
    drawContextA.stroke();
    // now the melon slice 
    drawContextA.strokeStyle = '#00ff00';
    drawContextA.beginPath();
    // arc() geometry has geomAngle.current starting  at 3:00 going up toward 12 counterclockwise.
    var topDeadCenter = -Math.PI/2;
    drawContextA.arc(center, center, radius, topDeadCenter, -geomAngle.current);
    drawContextA.stroke();
    // outer ring
    drawContextA.lineWidth = 4;
    drawContextA.strokeStyle = '#777777';
    drawContextA.beginPath();
    drawContextA.arc(center, center, radius, 0, -Math.PI * 2);
    drawContextA.stroke();
  }

  function MouseInfo(props) {
     return (<div id='mouseMoveID' 
         style={{ position:'relative', display:'none', border: '0 solid green',
                  display: 'grid', gridTemplateColumns: 'repeat(2, auto)'}}  >
			   <div>val: </div><div>{props.msg} {11}</div>
         <div>X: </div><div>{mouseX_st}</div>
         <div>Y: </div><div>{mouseY_st}</div>
         <div>angle: </div><div>{'geomAngle.: ' + toDegClock(geomAngle.current) + ' knobAngle: ' + toDegClock(knobAngle.current) }</div>
			</div>)
  }

  function DisplayA(props) {
    // console.log('vv ' + canvasWidthHeight.current)
    if (canvasWidthHeight.current == UNINIT_A) return (<div style={{ display:'none' }}/>)
    var topOffst = (canvasWidthHeight.current / 4) + 'px';
    return (
      <div id='DisplayA'  ref={displayA} 
           // note.. position:'relative' still takes space at top, so set height to 0 px
           style={{ top:topOffst , position:'relative', height:'0px',
                    border: '1` solid blue', gridTemplateColumns: 'repeat(2, auto)'}}  >
        <div>msg: </div><div>{props.msg} {11}</div>
      </div>)
 }

  function renderX() { // ported from class component render()
     // gridTemplateColumns: 'auto 1fr' .. [first col auto width, second uses remainder]
     // also repeat(2, 1fr) [2 cols each uses 1 unit] .... repeat(2, auto) [2 cols each auto-width]
     // note: this component sizes itself based on its parents width and border-width
    return (
      <div ref={thisReactNode} id='knobTop' style={{ border: '2px solid aqua'}}>
        {/* <MouseInfo msg="mouseinfo"/> */}
        <DisplayA msg="mouseinfo"/>
        <canvas id='knobCanvas' ref={canvasA}
          height={canvasWidthHeight.current}
          width={canvasWidthHeight.current}
        />
      </div>
    );
  }
  return renderX();
}

function toDegClock(rads) {
   return ((rads / (2 * Math.PI)) * 360 ).toFixed(0)
}

function knobCallbackA(newVal) {
   //console.log('knobCallbackA: ' + newVal)
}

function TestReactKnobInGrid() {
  useEffect(() => { // [] empty: run only once when component mounts like componentDidMount
     // nothing to do  
  }, []);
  return (
    <div style={{ width:'70%', border: '0px solid orange', display:'grid',  
         gridTemplateColumns: 'repeat(8, 1fr)'}}  >
      <div style={{fontSize: '1.2em', gridColumn: '1/8', textAlign: 'center', paddingRight: '10%'}} 
            onClick={() => { window.location.reload();}}>reload { LABEL }
      </div>
        <div  id='bigTopOne' style={{ gridColumn: '1/8', border: '1px solid red '}}>
          <div style={{border: '1px solid #777777', width: '75%'}} >
            <ReactKnob lineWidthPct={10} knobCallback={knobCallbackA}/></div>
        </div>
        {/* */}
        <div id='AAb' style={{gridColumn: '1/3', border: '1px solid red '}}>
          <ReactKnob lineWidthPct={10}  knobCallback={knobCallbackA}/></div>
        <div id='BB' style={{gridColumn: '3/8', border: '1px solid red'}}>
          <ReactKnob lineWidthPct={10}  knobCallback={knobCallbackA}/></div> 
        <div style={{gridColumn: '1/5', textAlign: 'center', paddingRight: '10%'}} >
          NOTE: ReactKnob is always SQUARE and assumes the WIDTH given to it by the
          parent element, in this case a grid div.
        </div>
      </div>
  )
}

// ---------- END snip to copy into react code -----------------------
export { ReactKnob, LABEL, TestReactKnobInGrid };

